import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Form, Card, DatePicker, Select, Button,message,notification} from 'antd'
import Utils from "../../../../utils";
import moment from "moment";
import ApiService from "../../../../services/Api.service";
import {useHistory} from "react-router-dom";

const RoomProcBook = (props)=> {
    let { loading , data} = props
    const [ form ] = Form.useForm()
    const history = useHistory()

    const onFinish = ()=> {
        let key = 'updatable'
        form.validateFields()
            .then((value)=> {
                if(!props?.isAuthenticated){
                    notification.warn({
                        message:"Masuk Terlebih dahulu",
                        description:"dibutuhkan masuk terlebih dahulu",
                        btn:<Button type={'primary'} ghost size={'small'} href={'/auth/login'}>Sign In</Button>
                    })
                }else{
                    message.loading({content:"Loading...",key})
                    // Reflect.set(value,'start',moment(value.start).format("YYYY-MM-DD"))
                    let formdata = new URLSearchParams()
                    formdata.append('room',data?._id ?? "")
                    formdata.append('amount',data?.price?.regular ?? "")
                    formdata.append('book_date',moment(value.start).format("YYYY-MM-DD"))

                    new ApiService({
                        url:`/api/v1/book`,
                        body:formdata
                    }).post()
                        .then((response)=> {
                            if(response?.error){
                                if(response?.status === 401){
                                    notification.error({
                                        placement:"topRight",
                                        message:response?.statusText ?? "unAuthorization",
                                        duration:5
                                    })
                                    setTimeout(()=> {
                                        history.replace('/auth/login')
                                        // window.location.replace('/auth/login')
                                    },2000)
                                }
                            }
                            console.log({response})
                            message.success({content:"Loading...",key,duration:2})
                        })
                        .catch((err)=> {
                            console.log({err})
                            message.error({content:"Loading...",key,duration:4})
                        })
                }
            })
            .catch((err)=> {
                console.log({err})
            })
    }

    return !loading && (
        <div className="w-full yid-card-proc-book">
            <div className="w-full p-4 rounded-xl bg-white mb-3 space-y-4 shadow">
                <div className="flex items-center gap-1">
                    <h3 className={'text-lg font-weight-bold p-0 m-0'}>{['Rp',Utils.numberFormat(data?.price?.regular,',')].join(" ")}</h3>
                    <span className={'italic font-semibold'}>{"/"+data?.unit ?? ""}</span>
                </div>
                <div className="w-full">
                    <Form
                        form={form}
                        onFinish={onFinish}
                        layout={'vertical'}
                        initialValues={{
                            unit:"month",
                            start:null
                        }}>
                        <div className="flex items-center flex-col w-full">
                            <Form.Item name={'start'} label={'From'} className={'w-full'} rules={[
                                {
                                    required:true,
                                    message:"Date can't be empty!"
                                }
                            ]}>
                                <DatePicker className={'w-full'}/>
                            </Form.Item>
                            <Form.Item name={'unit'} className={'w-full'}>
                                <Select className={'w-full'} size={'small'} >
                                    <Select.Option value={'month'} className={'px-2'}>Month</Select.Option>
                                    <Select.Option value={'year'} className={'px-2'}>Year</Select.Option>
                                </Select>
                            </Form.Item>
                        </div>
                        <Button type={'primary'} htmlType={'submit'} className={'w-full'}>Ajukan Sewa</Button>

                    </Form>
                </div>

            </div>
        </div>
    )
}

RoomProcBook.propTypes = {}
RoomProcBook.defaultProps = {}

export default connect(({Room,auth}) => {
    let {loading, error, message, data} = Room
    let {isAuthenticated} = auth
    return {loading, error, message, data,isAuthenticated}
},{})(React.memo(RoomProcBook))