import React, {useEffect, useState} from 'react'
import PropTypes from "prop-types"
import {connect} from 'react-redux'
import Container from "../../../../components/layout-components/container";
import {Row, Col, Card, Image, Tooltip, Divider} from 'antd'
import {_getRoomsDetail} from "../../../../redux/actions";
import {useParams} from "react-router-dom";
import {first} from 'lodash'
import RoomFacility from "./RoomFacility";
import RoomManaged from "./RoomManaged";
import RoomProcBook from "./RoomProcBook";

const RoomsDetail = (props) => {
    let {_getRoomsDetail} = props
    let {id} = useParams()
    const [type, setType] = useState('')
    console.log({id})

    useEffect(() => {
        _getRoomsDetail(id, {}, '')
    }, [_getRoomsDetail])


    return !props?.loading && (
        <div className="w-full py-6">
            <Container size={'md'}>
                <React.Fragment>
                    <div className={'w-full overflow-hidden rounded-[10px] relative'}>

                        <div className="grid grid-cols-12 gap-2">
                            {
                                Array.isArray(props?.data?.images) && props?.data?.images.length > 0 ? (
                                    <div className="col-span-8 border" style={{height:440,position:"relative"}}>
                                        <div style={{position:"absolute",top:0,left:0,width:"100%",height:440}}>
                                            <Image
                                                className={'w-100'}
                                                width={"100%"}
                                                style={{
                                                    height:440,
                                                    width:"100%",
                                                    objectFit:" cover"
                                                }}
                                                fallback={'/img/image-notfound.png'}
                                                src={[first(props?.data?.images)?.prefix_url, first(props?.data?.images)?.filename].join("/")}
                                            />
                                        </div>
                                    </div>
                                ) : (
                                    <div className="col-span-8">
                                        <img
                                            className={'w-full object-cover h-[440px]'}
                                            src={'/img/image-notfound.png'}
                                            alt={'/img/image-notfound.png'}
                                        />
                                    </div>
                                )
                            }

                            <div className="col-span-4">
                                <div className="flex flex-col gap-2 w-full" style={{height: 440}}>
                                    {
                                        Array.isArray(props?.data?.images) && props?.data?.images.length > 1 ? (
                                            <div className={'w-full'}
                                                 style={{position: 'relative', height: "220px", width: '100%'}}>
                                                <div style={{
                                                    width: '100%',
                                                    // height: 220,
                                                    // position: "absolute",
                                                    // top: 0,
                                                    left: 0,
                                                    overflow: "hidden"
                                                }}>
                                                    <Image
                                                        className={'w-100'}
                                                        fallback={'/img/image-notfound.png'}
                                                        src={[props?.data?.images[1]?.prefix_url, props?.data?.images[1]?.filename].join("/")}
                                                    />
                                                </div>

                                            </div>
                                        ) : (
                                            <div className="w-full">
                                                <img
                                                    className={'w-full object-cover h-[215px]'}
                                                    src={'/img/image-notfound.png'}
                                                    alt={'/img/image-notfound.png'}
                                                />
                                            </div>
                                        )
                                    }
                                    {
                                        Array.isArray(props?.data?.images) && props?.data?.images.length > 2 ? (
                                            <div className={'w-full'}
                                                 style={{position: 'relative', height: "215px", width: '100%'}}>
                                                <div style={{
                                                    width: '100%',
                                                    height: 220,
                                                    position: "absolute",
                                                    top: 0,
                                                    left: 0,
                                                    overflow: "hidden"
                                                }}>
                                                    <Image
                                                        className={'w-100'}
                                                        fallback={'/img/image-notfound.png'}
                                                        src={[props?.data?.images[2]?.prefix_url, props?.data?.images[2]?.filename].join("/")}
                                                    />
                                                </div>

                                            </div>
                                        ) : (
                                            <div className="col-span-8">
                                                <Image
                                                    style={{
                                                        width: "100%",
                                                        height: "215px",
                                                        objectFit: "cover"
                                                    }}
                                                    fallback={'/img/image-notfound.png'}
                                                    src={'/img/image-notfound.png'}
                                                />
                                            </div>
                                        )
                                    }
                                </div>
                            </div>
                        </div>
                        <div
                            className="absolute bottom-0 left-0 z-[10] w-full h-14 flex items-center flex-end justify-end px-2">
                            <Tooltip title={'Total Picture'}>
                                <div
                                    className="w-10 h-10 rounded-lg bg-white flex items-center justify-center cursor-pointer">
                                    <span className={'text-gray-700'}>10</span>
                                </div>
                            </Tooltip>
                        </div>
                    </div>
                    <div className={'mt-4'}>
                        <div className="grid grid-cols-12 gap-2 position-relative">
                            <div className="col-span-8">
                                <div className="!space-y-2">
                                    <h1 className={'!text-2xl !truncate p-0 m-0 font-lexend font-weight-bold'}>{props?.data?.name ?? "-"}</h1>
                                    <p className={'my-0 p-0'}>{props?.data?.description ?? ""}</p>
                                </div>

                                <Divider/>

                                <RoomFacility data={{...props?.data?.facility}}/>

                                <Divider/>

                                <RoomManaged/>
                            </div>
                            <div className="col-span-4 relative">
                                <RoomProcBook/>
                            </div>
                        </div>
                    </div>

                </React.Fragment>
            </Container>
        </div>
    )
}

RoomsDetail.propTypes = {
    _getRoomsDetail: PropTypes.func.isRequired
}
RoomsDetail.defaultProps = {
    _getRoomsDetail: (id = 0) => {
        console.log({id})
    }
}

export default connect(({Room}) => {
    let {loading, error, message, data} = Room
    return {loading, error, message, data}
}, {_getRoomsDetail})(React.memo(RoomsDetail))