import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Row, Col, Image, Avatar} from 'antd'
import Flex from "../../../../components/shared-components/Flex";
import CardCustom from "../../../../components/shared-components/cards/card-custom";


const RoomManaged = (props)=> {
    let { loading  } = props
    const [data,setData] = React.useState(()=> {
        if(typeof(props?.data?.villa) !== "undefined"){
            return [ {...props?.data?.villa}]
        }
        return []
    })
    return !loading && (
        <div className="w-full space-y-6">
            <h3>Dikelola Oleh</h3>
            <div className="w-full max-w-[420px]">
                {data.map((item,index)=>{
                    return (
                        <CardCustom
                            {...item}
                            key={`card-custom-index-${index}`}
                            title={item?.name ?? item?.villaName ?? "-"}
                            url={`/villa/p/${item?._id ?? item?.id ??  item?.name?.replace(/ /g,'-').toString().toLowerCase() ?? "undefined"}`}
                        />
                    )
                })}
            </div>
        </div>
    )
}

RoomManaged.propTypes = {}
RoomManaged.defaultProps = {}
export default connect(({Room}) => {
    let {loading, error, message, data} = Room
    return {loading, error, message, data}
})(React.memo(RoomManaged))