import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { Button } from 'antd'


const RoomFacility = (props)=> {
    const [data,setData] = React.useState(()=> {
        if(typeof(props.data) !== "undefined" && Object.keys(props.data).length > 0){
            let newData = []
            Object.keys(props.data).forEach((key)=> {
                if(key !== "other" && key !== "bed_type"){
                    newData.push({
                        label:`${key}`.replace(/_/g,' '),
                        value:props.data[key]
                    })
                }
            })
            return newData
        }
        return []
    })
    const [dataStr,setDataStr] = React.useState(()=> {
        if(typeof(props.data) !== "undefined" && Object.keys(props.data).length > 0){
            let newData = []
            Object.keys(props.data).forEach((key)=> {
                if(key === "other" || key === "bed_type"){
                    newData.push({
                        label:`${key}`.replace(/_/g,' '),
                        value:props.data[key]
                    })
                }
            })
            return newData
        }
        return []
    })
    return (
        <div className="w-full !space-y-6">
            <h3>Facility</h3>

            <div className="w-full flex items-center !gap-4">
                {data.sort((a,b)=> Number(b.value) - Number(a.value)).map((item,index)=> {
                    return (
                        <Button size={'small'} type={item?.value ? 'primary':"default"} disabled={!(item.value)} ghost={item.value} key={`${item?.label}-${index}`}>{item?.label}</Button>
                    )
                })}
            </div>

            <div className="w-full !space-y-4">
                {dataStr.map((item,index)=> {
                    return (
                        <div className="w-full space-y-3">
                            <h4 className={'!capitalize'}>{item.label}</h4>
                            <div dangerouslySetInnerHTML={{__html:item.value}}/>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

RoomFacility.propTypes = {
    data: PropTypes.shape({
        ac:PropTypes.bool,
        tv:PropTypes.bool,
        wifi:PropTypes.bool,
        smooking:PropTypes.bool,
    }).isRequired
}
RoomFacility.defaultProps = {
    data:{}
}

export default connect(()=> {
    return {}
},{})(RoomFacility)